<template>
  <LuckyWheel
  style="margin: auto;"
      ref="myLucky"
      width="400px"
      height="400px"
      :prizes="prizes"
      :blocks="blocks"
      :buttons="buttons"
      @start="startCallback"
      @end="endCallback"
  />
</template>

<script >



export default {
  data () {
    return {
      blocks: [
        { padding: '10px', background: '#869cfa' }
      ],
      prizes: [
        { background: '#e9e8d2', fonts: [{ text: '他来' }] },
        { background: '#b8c5f2', fonts: [{ text: '我去' }] },
      ],
      buttons: [
        { radius: '40%', background: '#617df2' },
        { radius: '35%', background: '#afc8ff' },
        {
          radius: '30%', background: '#869cfa',
          pointer: true,
          fonts: [{ text: '开始', top: '-10px' }]
        }
      ]
    }
  },
    methods: {
    // 点击抽奖按钮会触发star回调
    startCallback () {
      // 调用抽奖组件的play方法开始游戏
      this.$refs.myLucky.play()
      // 模拟调用接口异步抽奖
      setTimeout(() => {
        // 假设后端返回的中奖索引是0
        let index = Math.floor(Math.random() * 100) + 1
        // 调用stop停止旋转并传递中奖索引

        if(index>0&&index<=50){
           this.$refs.myLucky.stop(0)
        }
         if(index>50&&index<=100){
           this.$refs.myLucky.stop(1)
        }
        

      }, 3000)
    },
    // 抽奖结束会触发end回调
    endCallback (prize) {
      console.log(prize)
      alert(prize['fonts'][0]['text']);
    },
  }
  
}
</script>

